<template>
  <div id="invoice-top">
    <div
      class="add flex"
      @click="show_addOredit = true,editDataForm = {}"
    >
      <van-icon
        name="add"
        color="#FD7E43"
        style="padding-right:8px;"
      />添加商品
    </div>
    <div class="cleaning-box">
      <div
        v-for="(item,i) in leaningAll"
        :key="i"
        class="cleaning-center"
        @click="editOrRemove(item)"
      >
        <div>{{item.costName}}</div>
        <div class="flex">￥{{item.price}}/{{ item.unit }}
          <van-icon
            name="arrow"
            color="#B9B6BA"
            style="padding-left:8px;"
          />
        </div>
      </div>
    </div>
    <van-popup
      position='bottom'
      :close-on-click-overlay='false'
      v-model="show_editOrRemove"
    >
      <div
        class="p10 c_e4"
        @click="removeItem"
      >删除</div>
      <div
        class="p10"
        @click="show_editOrRemove = false, show_addOredit = true"
      >编辑</div>
      <div
        style="height:6px;background:#f5f5f5;"
        @click="show_editOrRemove = false"
      ></div>
      <div
        class="p10 c_88"
        @click="show_editOrRemove = false"
      >取消</div>
    </van-popup>
    <van-dialog
      v-model="show_addOredit"
      :before-close="confirmEdit"
      :title="editDataForm.id?'编辑商品':'添加商品'"
      confirmButtonText='确定'
      confirmButtonColor='#fd7e43'
      cancelButtonColor='#7A7A7A'
      show-cancel-button
    >
      <van-field
        v-model="editDataForm.costName"
        label="名称"
        placeholder="请输入商品名称"
      />
      <van-field
        v-model="editDataForm.unit"
        label="单位"
        placeholder="请输入个、瓶、袋、条等单位"
      />
      <van-field
        v-model="editDataForm.price"
        label="价格"
        type='number'
        placeholder="请输入零售价"
      />
      <van-field
        v-model="editDataForm.free"
        label="免费限额"
        type='number'
        placeholder="请输入免费试用数量"
      />
    </van-dialog>
  </div>
</template>
<script>
import { GetBusinessHotelCostQuery, PostBusinessHotelCostAdd, PostBusinessHotelCostEdit, PostBusinessHotelCostRemove } from '@/api/api_cleaning_or_goods';
import { Toast, Dialog } from 'vant';
export default {
    data() {
        return {
            selected: '0',
            leaningAll: [],
            unitAll: [],
            editDataForm: {
                hotelId: this.$route.query.hotelId,
                hotelCostType: '1',
                id: null,
                costName: null,
                price: null,
                unit: null,
                free: null
            },
            show_editOrRemove: false,
            show_addOredit: false
        };
    },
    created() {
        this.$store.commit('setHeader', '商品设置');
        this.getInfo();
    },
    methods: {
        getInfo() {
            let data = {
                hotelId: this.$route.query.hotelId,
                hotelCostType: '1'
            };
            GetBusinessHotelCostQuery(data).done((res) => {
                this.leaningAll = res.data;
            });
        },
        editOrRemove(item) {
            this.currentInfo = item;
            this.editDataForm.id = item.id;
            this.editDataForm.costName = item.costName;
            this.editDataForm.price = item.price;
            this.editDataForm.unit = item.unit;
            this.editDataForm.free = item.free;
            this.show_editOrRemove = true;
        },
        removeItem() {
            this.show_editOrRemove = false;
            Dialog.confirm({
                title: this.currentInfo.costName,
                message: '确定删除?'
            })
                .then(() => {
                    PostBusinessHotelCostRemove({ id: this.currentInfo.id }).done((res) => {
                        this.getInfo();
                    });
                })
                .catch(() => {});
        },
        confirmEdit(action, done) {
            if (action == 'confirm') {
                if (!this.editDataForm.costName) {
                    Toast('请输入商品名称');
                    done(false);
                    return;
                }
                if (!this.editDataForm.unit) {
                    Toast('请输入个、瓶、袋、条等单位');
                    done(false);
                    return;
                }
                if (!this.editDataForm.price) {
                    Toast('请输入零售价');
                    done(false);
                    return;
                }
                if (this.editDataForm.id) {
                    (this.editDataForm_ = {
                        id: this.editDataForm.id,
                        costName: this.editDataForm.costName,
                        price: this.editDataForm.price,
                        unit: this.editDataForm.unit,
                        free: this.editDataForm.free
                    }),
                        PostBusinessHotelCostEdit(this.editDataForm_).done((res) => {
                            done(false);
                            this.show_addOredit = false;
                            this.getInfo();
                        });
                } else {
                    (this.addDataForm = {
                        hotelId: this.$route.query.hotelId,
                        hotelCostType: '1',
                        costName: this.editDataForm.costName,
                        price: this.editDataForm.price,
                        unit: this.editDataForm.unit,
                        free: this.editDataForm.free
                    }),
                        PostBusinessHotelCostAdd(this.addDataForm).done((res) => {
                            done(false);
                            this.show_addOredit = false;
                            this.getInfo();
                        });
                }
            } else if (action == 'cancel') {
                done(false);
                this.show_addOredit = false;
            }
        }
    }
};
</script>
<style scoped>
.add {
    color: #fd7e43;
    background: #fff;
    margin: 12px 0;
    font-size: 0.9rem;
    padding: 8px 0;
}
.cleaning-box {
    background: #fff;
    color: #000;
    font-size: 0.8rem;
    position: fixed;
    left: 0;
    right: 0;
    height: calc(100% - 130px);
    overflow: auto;
}
.cleaning-center {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    border-bottom: 1px solid #f8f6f9;
}
.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}
.p10 {
    padding: 10px;
    border-bottom: 1px solid #f8f6f9;
    font-size: 0.8rem;
    color: #000;
}
.c_e4 {
    color: #e46a69;
}
.c_88 {
    color: #888;
}
::v-deep .van-dialog__header {
    font-weight: bold;
    color: #000;
    padding: 20px 0 10px 0;
}
/deep/ .van-cell {
    align-items: center;
}
/deep/ .van-field__value {
    background: #f0eff2;
    color: #000;
    border-radius: 4px;
}
/deep/ .van-field__label {
    color: #000;
    font-weight: bold;
    width: 5.5em;
    text-indent: 1em;
}
/deep/ .van-field__control {
    text-indent: 1em;
    padding: 4px 0;
}
/deep/ .van-cell::after {
    border: none;
}
</style>



